<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:01:32
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="部门名称：" prop="name">
          <a-input
            class="default-input"
            placeholder="请输入部门名称"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="部门等级：">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择部门等级"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              等级1
            </a-select-option>
            <a-select-option value="2">
              等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="部门编码：" prop="code">
          <a-input
            class="default-input"
            placeholder="请输入部门编码"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="行政区划：" prop="divide">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择行政区划"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              市辖区
            </a-select-option>
            <a-select-option value="2">
              市辖区
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="电话号码：" prop="phone">
          <a-input
            class="default-input"
            placeholder="请输入电话号码"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="地址：" prop="address">
          <a-input
            class="default-input"
            placeholder="请输入地址"
            :disabled="isShow"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        name: "",
        code: "",
        divide: "",
        phone: "",
        address: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入部门名称！",
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入部门编码！",
            trigger: "blur"
          }
        ],
        divide: [
          {
            required: true,
            message: "请选择行政区划！",
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            message: "请输入电话号码！",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "请输入地址！",
            trigger: "blur"
          }
        ],
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
