<template>
  <div class="menber-list">
    <modal
      title="查看成员"
      :visible="visible"
      width="44.58vw"
      height="30.78vw"
      @close="$emit('close')"
    >
      <img
        slot="modalBg"
        class="modal-bg"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <div class="details-list-search">
        <a-form-model :model="form" :layout="formLayout">
          <a-form-model-item label="成员名称：">
            <a-input class="default-input name" v-model="form.name"></a-input>
          </a-form-model-item>
          <a-form-model-item label="手机号码：">
            <a-input class="default-input" v-model="form.name"></a-input>
          </a-form-model-item>
        </a-form-model>
      </div>
      <div class="btn-box">
        <a-button class="operation-button">
          <span class="iconfont iconfont-14 icon-sousuo1"></span>
          <span class="btn-title">查询</span>
        </a-button>
        <a-button class="operation-button" @click="addMessage">
          <span class="iconfont iconfont-16 icon-bianji2"></span>
          <span class="btn-title">新增</span>
        </a-button>
      </div>
      <div class="details-list-contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :scroll="{ y: '16.6vw' }"
        >
        <div slot="operation" class="operation">
            <div class="count" @click="editMessage()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title">编辑</span>
            </div>
            <div class="count" @click="deleteMessage()">
              <span class="iconfont iconfont-16 icon-del"></span>
              <span class="title">删除</span>
            </div>
          </div>
        </a-table>

        <a-pagination
          v-model="current"
          :total="total || 1"
          :page-size="defaultPageSize"
          :item-render="itemRender"
          @change="onPaginationChange"
        />
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
    </modal>
    <!-- 新增弹窗 start -->
    <member-modal
      :title="'新增成员'"
      :visible="addModal"
      @close="closeModal"
    ></member-modal>
    <!-- 新增弹窗 end -->
    <!-- 编辑弹窗 start -->
    <member-modal
      :title="'编辑成员'"
      :visible="editModal"
      @close="closeModal"
    ></member-modal>
    <!-- 编辑弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Modal from "../../components/Modal";
import MemberModal from "./memberModal"
import moment from "moment";
import deletemodal from "../../components/deleteModal";

const columns = [
  {
    title: "序列",
    dataIndex: "sequence",
    key: "sequence",
    width: "3vw"
  },
  {
    title: "成员名称",
    dataIndex: "name",
    key: "name",
    width: "5vw"
  },
  {
    title: "用户账号",
    dataIndex: "account",
    key: "account",
    width: "6vw"
  },
  {
    title: "手机号码",
    dataIndex: "phone",
    key: "phone",
    width: "10vw"
  },
  {
    title: "职务",
    dataIndex: "duty",
    key: "duty",
    width: "4vw"
  },
  {
    title: "角色",
    dataIndex: "role",
    key: "role",
    width: "4vw"
  },
  {
    title: "操作",
    dataIndex: "operation",
    key: "operation",
    width: "8vw",
    scopedSlots: { customRender: "operation" }
  }
]; //表头信息

export default {
  components: {
    Modal,
    MemberModal,
    deletemodal
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      columns: columns,
      data: [],
      selectedRowKeys: [],
      selectedRows: [],
      defaultPageSize: 7,
      total: 100,
      current: 1,
      formLayout: "inline",
      form: {
        name: "",
        type: "1"
      },
      addModal: false, //新增操作弹窗是否显示
      editModal: false, //编辑操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    }
  },
  mounted() {
    for (let i = 0; i < 9; i++) {
      this.data.push({
        key: i,
        sequence: i,
        name: "张三",
        account: "admin",
        phone: "18356855201",
        duty: "安检员",
        role: "管理员"
      });
    }
  },
  methods: {
    moment,
    itemRender(current, type, originalElement) {
      if (type === "prev") {
        return "上一页";
      } else if (type === "next") {
        return "下一页";
      } else if (type === "page") {
        return current;
      }
      return originalElement;
    },

    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRows = selectedRows;
      this.selectedRowKeys = selectedRowKeys;
    },
    saveEquipment() {
      this.$emit("saveEquipment", this.selectedRows);
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.deleteModal = false;
    },
    /**
     * 点击新增打开弹窗
     */
    addMessage() {
      this.addModal = true;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    }
  },
};
</script>

<style lang="less" scoped></style>
